import { HandleProvider } from './../../../providers/handle/handle';
import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { NativeService } from '../../../services/services';

/**
 * Generated class for the FilePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-file',
  templateUrl: 'file.html',
})
export class FilePage {
  imageUrl: string = '';
  tmpImageBase64: string = ''

  result:string
  @ViewChild('fileInput') fileInput;
  constructor(public navCtrl: NavController,
     public navParams: NavParams,
     public nativeService: NativeService,
     public handle: HandleProvider) {
  }

  ionViewDidLoad() { 
  }

  openPhoneGallery() {
    if (this.nativeService.isMobile()) {
      this.nativeService.getFullPictureByPhotoLibrary({//从相册多选
        destinationType: 0//期望返回的图片格式,0图片字符串
      }).subscribe(img => {
         this.imageUrl = img;
      }, (err) => {
        this.nativeService.showToast(err)
      });
    } else {
      this.fileInput.nativeElement.click(); 
    }
  }

  processWebImage(event) {
    let filesList = event.target.files;
    for(let file of filesList){ 
      let reader = new FileReader();
      reader.onload = (readerEvent) => { 
        let imageData = (readerEvent.target as any).result; 
        this.imageUrl = imageData;

        this.tmpImageBase64 = this.imageUrl.substr(0,50)
      };
      reader.readAsDataURL(file);
    }  
  }

  saveBase64toFile(){
    this.nativeService.saveBase64toFile(this.imageUrl).subscribe(res=>{
      this.handle.generateInfo('saveBase64toFile',res)
      this.result = JSON.stringify(res)
      this.imageUrl = res.result.nativeURL;
    },err=>{
      this.handle.generateError('saveBase64toFile',err)
      this.result = JSON.stringify(err)
    })
  }

}



